/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
  color: #25282a;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  line-height: 1.6;
}

blockquote {
  margin: 0 25px;
}

pre {
  max-width: 100%;
  padding: 1.6em;
  overflow: auto;
  background: #bebebe;
}

[class*='font-'] {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #000;
  font-size: 30px;
  line-height: 1;
}

/*--------------------------------------------------------------
## Base Layout
--------------------------------------------------------------*/
html, body {
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -ms-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  height: 100%;
}

html {
  box-sizing: border-box;
}

/*--------------------------------------------------------------
## Layout
--------------------------------------------------------------*/
.wrapper {
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
  padding: 0 15px;
}

.wrapper-lg {
  max-width: 1800px;
}

/*--------------------------------------------------------------
## Animations
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Parts
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Buttons
--------------------------------------------------------------*/
.btn {
  display: inline-block;
  padding: 15px 36px;
  transition: 350ms ease;
  font-size: 13px;
  text-align: center;
  text-transform: uppercase;
}

.btn:active, .btn:focus {
  outline: none;
}

@media (max-width: 1024.98px) {
  .btn {
    padding: 7px 15px;
    font-size: 11px;
  }
}

.btn--red {
  padding: 25px 10px;
  background-color: #f85959;
  color: #fff;
  width: 160px;
  display: block;
  font-size: 16px;
  text-transform: capitalize;
}

@media screen and (max-width: 1199px) {
  .btn--red {
    margin-top: 20px;
  }
}

@media screen and (max-width: 767px) {
  .btn--red {
    width: 100%;
    font-size: 14px;
  }
}

.btn-lg {
  background-color: #f85959;
  color: #fff;
  width: 100%;
  outline: none;
  text-transform: capitalize;
  font-size: 16px;
  border: 1px solid #f85959;
  transition: 350ms ease;
  border-radius: 2px;
  padding: 20px 40px;
}

.btn-lg:hover {
  background-color: #fff;
  color: #f85959;
}

.btn--green {
  padding: 3px 5px;
  font-size: 14px;
  font-weight: 300;
  background-color: #8bc34a;
  color: #fff;
  text-transform: capitalize;
  transition: 350ms ease;
}

.btn--green:hover {
  background-color: #6c943c;
}

.btn--ghost {
  background-color: transparent;
  border: 1px solid #f85959;
  color: #f85959;
  padding: 12px 20px;
  text-transform: capitalize;
  border-radius: 100px;
  font-size: 16px;
  transition: 350ms ease;
}

.btn--ghost:hover {
  background-color: #f85959;
  color: #fff;
}

@media screen and (max-width: 767px) {
  .btn--ghost {
    font-size: 14px;
    padding: 9px 10px;
  }
}

.btn--fill {
  background-color: #f85959;
  border: 1px solid #f85959;
  color: #fff;
  padding: 15px 60px;
  text-transform: capitalize;
  border-radius: 100px;
  font-size: 16px;
  transition: 350ms ease;
}

.btn--fill:hover {
  background-color: #fff;
  color: #f85959;
}

@media screen and (max-width: 767px) {
  .btn--fill {
    font-size: 14px;
    padding: 9px 10px;
  }
}

.btn--subscribe {
  padding: 9px 36px;
  text-transform: capitalize;
  background-color: transparent;
  color: #fff;
  outline: none;
  border: 1px solid #fff;
  font-size: 16px;
}

